/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.SidebarLayout.LayoutEditor__sections {
  display: flex; }

.LayoutEditor__region--content {
  flex: 3;
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  padding-top: 5px;
  margin-right: 20px; }

.RegionContent.LayoutEditor__region--content {
  flex: 3;
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  padding-left: 5px;
  margin-right: 20px; }

.SidebarLayout.RegionSidebar.LayoutEditor__right--sidebar {
  width: 360px;
  min-width: 360px;
  margin-top: 8px; }

.LayoutEditor__header--sections-wrapper,
.LayoutEditor__related--sections-wrapper {
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  width: 100%; }

.LayoutEditor__section--columns {
  display: flex;
  justify-content: center;
  padding-left: 3px;
  padding-right: 3px; }

.LayoutEditor__section--column {
  flex: 1;
  flex-grow: 0;
  padding-right: 15px;
  padding-left: 15px; }

.LayoutEditor__properties--sidebar-wrapper {
  padding-right: 3px; }

.Section__addon .Button--type-link {
  font-size: 13px; }

.LayoutEditor__section {
  width: 100%; }
  .LayoutEditor__section .slds-section {
    padding-top: 1rem;
    padding-bottom: 1rem; }

.LayoutEditor__region--content .Section__title.slds-section__title {
  cursor: move;
  border-bottom: 0; }

.LayoutEditor__section--header .Section__title--text,
.LayoutEditor__section--related .Section__title--text {
  font-weight: 700; }

.PageLayout__field--header--empty {
  margin-bottom: 20px; }

.LayoutField.LayoutField__empty {
  height: 32px; }

.LayoutField.LayoutField__empty.LayoutField__placeholder {
  border: 1px #dddbda dashed;
  border-radius: 3px; }

.LayoutField__placeholder {
  margin-bottom: 20px; }

.HeaderSection__drop--target {
  width: 100%;
  padding-bottom: 1rem; }

.LayoutEditor__section--header .Section.slds-is-open .Section__content {
  overflow: hidden; }

.LayoutEditor__section--header.dropallowed.hovering.Section--highlighted .Section__content {
  border: 0; }

.LayoutEditor__section--header .Section__title--text,
.LayoutEditor__section--related .Section__title--text {
  max-width: calc(100vw - 700px); }

.HeaderSection__drop--target.dropallowed.hovering {
  border: 2px #dddbda dashed; }
  .HeaderSection__drop--target.dropallowed.hovering .LayoutEditor__section {
    opacity: 0.1; }

.LayoutEditor__layout--heading {
  width: 100%;
  display: flex;
  justify-content: space-between; }
  .LayoutEditor__layout--heading .PageButton__page--settings {
    position: relative;
    top: -26px;
    left: 14px;
    /* stylelint-disable max-nesting-depth */ }
    .LayoutEditor__layout--heading .PageButton__page--settings .PageSettings__icon.error {
      fill: #c23934; }

.PageLayout__field.Placeholder__field:hover .LayoutField__box,
.PageLayout__field.Placeholder__field:focus .LayoutField__box,
.PageLayout__field.Placeholder__field:active .LayoutField__box {
  border-color: #dddbda;
  outline: 0;
  background: transparent; }

.HeaderSection__column {
  padding: 15px 15px 0;
  flex-grow: 0;
  overflow: hidden;
  width: 100%; }
  .HeaderSection__column .EmptyLayoutSectionMessage {
    margin-top: -15px; }

.LayoutFieldPicker .ModalContent {
  min-height: 345px;
  overflow: hidden;
  display: flex;
  flex-flow: column; }
  .LayoutFieldPicker .ModalContent .Menu.slds-dropdown {
    flex: 2; }
  .LayoutFieldPicker .ModalContent .Menu__list {
    min-height: 92%;
    overflow-y: auto; }

.LayoutFieldPicker .LayoutFieldPicker__add--fields {
  display: flex;
  flex-flow: column;
  max-height: 50px;
  min-height: 50px;
  background: #fff;
  z-index: 90002;
  padding-top: 5px;
  padding-left: 15px;
  flex: 1; }
  .LayoutFieldPicker .LayoutFieldPicker__add--fields .slds-form-element__control {
    display: flex;
    flex-flow: row;
    justify-content: space-evenly; }

.Draggable__section--header,
.Draggable__section--related,
.Draggable__field,
.Draggable__field--related,
.Draggable__section--header:focus,
.Draggable__section--related:focus,
.Draggable__field:focus,
.Draggable__field--related:focus {
  outline: 0; }

.Draggable__section--header,
.Draggable__section--related {
  margin-top: 20px; }

.Draggable__section--related:first-child,
.Draggable__section--header:first-child {
  margin-top: 0; }

.Draggable__section--dragging .Section.Section__bordered {
  margin-top: 0;
  margin-bottom: 0; }

.Section.Section__bordered {
  background: #fff;
  border-radius: 4px; }

.slds-section__title-action {
  background: #fff; }

.Draggable__section--dragging .LayoutEditor__section--container,
.Draggable__section--dragging .LayoutEditor__section--related {
  border: 2px #dddbda dashed; }

.LayoutEditor__section--columns .HeaderSection__column:hover {
  background: #fafaf9; }

.AddSpace__menu--icon {
  height: 10px;
  width: 10px; }

.LayoutEditor__formcontent {
  padding: 0 10px;
  min-height: 100vh; }

.Section__buttons {
  padding-top: 10px;
  width: 100%; }

.Child__only {
  margin: 0; }

.sticky .PropertiesSideBar__container {
  margin-top: 75px; }

.Button--type-link.LayoutEditor-add--button {
  margin-right: 10px;
  padding: 0 0.5rem; }
  .Button--type-link.LayoutEditor-add--button .slds-m-right--x-small {
    margin-right: 0; }

.LayoutEditor-border--bottom {
  margin: 20px 0;
  border-top: 1px solid #dddbda;
  width: 100%; }

.column-space {
  border-left: 1px solid #dddbda;
  margin-right: 12px; }

.FieldProperties__container {
  height: calc(100vh - 80px);
  display: flex;
  flex-direction: column; }

.LayoutEditor__setting-icon {
  opacity: 0; }

.ButtonConfig__dropdown:hover .LayoutEditor__setting-icon {
  opacity: 1; }

.LayoutEditor__add--page-button {
  border: 1px dashed #dddbda; }
