/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Icon__svmx.Icon--size-xx-small svg {
  height: 12px;
  width: 12px; }

.Icon__svmx.Icon--size-x-small svg {
  height: 16px;
  width: 16px; }

.Icon__svmx.Icon--size-small svg {
  height: 24px;
  width: 24px; }

.Icon__svmx.Icon--size-medium svg {
  height: 32px;
  width: 32px; }

.Icon__svmx.Icon--size-large svg {
  height: 48px;
  width: 48px; }

.Icon__svmx.Icon--theme-info .svg-foreground {
  fill: #706e6b; }

.Icon__svmx.Icon--theme-success .svg-foreground {
  fill: #04844b; }

.Icon__svmx.Icon--theme-warning .svg-foreground {
  fill: #ffb75d; }

.Icon__svmx.Icon--theme-error .svg-foreground {
  fill: #c23934; }

.Icon__svmx.Icon--disabled .svg-foreground {
  fill: #dddbda; }

.Button .Icon__svmx .svg-foreground,
.BuilderHeader__item .Icon__svmx .svg-foreground,
.BuilderHeader__nav-item .Icon__svmx .svg-foreground {
  fill: currentColor; }

.Button .Icon.Icon__svmx .Icon-svg.Icon--size-large-svg {
  height: 24px;
  width: 24px; }

.Button .Icon.Icon__svmx .Icon-svg.Icon--size-small-svg {
  height: 16px;
  width: 16px; }

.Button .Icon.Icon__svmx .Icon-svg.Icon--size-x-small-svg {
  height: 14px;
  width: 14px; }

.Button .Icon.Icon__svmx .Icon-svg.Icon--size-xx-small-svg {
  height: 12px;
  width: 12px; }
