/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Tabs .Tab__item {
  white-space: nowrap; }
  .Tabs .Tab__item .slds-icon-red {
    fill: #d4504c;
    margin-left: 4px; }

.Tabs .Tab__item--disabled:hover, .Tabs .Tab__item--disabled::after {
  background-color: transparent; }

.Tabs .Tab__link {
  float: left; }

.Tabs .Tab__anchor--disabled {
  color: #b0adab;
  cursor: not-allowed; }
  .Tabs .Tab__anchor--disabled:hover, .Tabs .Tab__anchor--disabled:focus {
    color: #b0adab; }

.Tabs .Tab__anchor--error {
  color: #d4504c; }
  .Tabs .Tab__anchor--error:link, .Tabs .Tab__anchor--error:visited, .Tabs .Tab__anchor--error:hover, .Tabs .Tab__anchor--error:active, .Tabs .Tab__anchor--error:focus {
    color: #d4504c;
    text-decoration: none; }

.Tabs .TabItem__overflow .more-tab-scoped {
  width: 70px; }

.Tabs .TabItem__overflow .more-tab-default {
  width: 60px; }

.Tabs .TabItem__overflow a {
  text-decoration: none; }
