/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.PropertiesSideBar__container .Section.slds-is-open .Section__content {
  overflow: visible;
  padding-right: 5px;
  padding-left: 5px; }
  .PropertiesSideBar__container .Section.slds-is-open .Section__content .Button--type-link {
    margin-left: 5px; }
    .PropertiesSideBar__container .Section.slds-is-open .Section__content .Button--type-link.FieldProperties__lookup-text {
      margin-left: 5px; }

.FieldProperties__row-element {
  padding-bottom: 5px;
  margin-bottom: 5px;
  border-bottom: 1px solid #dddbda; }

.FieldProperties__toggle-row {
  padding-bottom: 10px; }

.FieldProperties__lookup-name {
  padding-top: 6px; }

.Icon.FieldProperties__lookup-check--icon {
  fill: #0070d2; }

.Icon.FieldProperties__lookup-check--icon--disable {
  opacity: "0.5"; }

.Icon.FieldProperties__lookup-formfill-check--icon {
  fill: #0070d2; }

.FieldProperties__lookup-container .LookupSection__label {
  padding-top: 5px;
  text-align: right; }

.FieldProperties__lookup-context {
  padding-top: 15px;
  padding-bottom: 6px; }

.LookupContext__serch-lookuptable {
  padding-left: 20px; }

.PropertiesSideBar__container
.FieldProperties__remove-btn.Button--type-neutral:not([disabled]) {
  color: #c23934; }

.LookupSection__entity-picker .EntityPicker .InputWrapper,
.LookupSection__entity-picker .EntityPicker .Lookup__menu {
  width: 100%; }
