.SectionPropertiesModal__tab.Conditions {
  padding: 1rem 0 0;
}

.SectionPropertiesModal__tab-container {
  height: calc(100vh - 350px);
  overflow-y: hidden;

  .LayoutEditor__tabs {
    min-height: auto;
    margin-bottom: 10px;

    .Tab__link {
      &.slds-tabs_scoped__link {
        background-clip: inherit;
      }
    }
  }
}

.SectionPropertiesModal__tab {
  &.slds-tabs__content {
    min-height: calc(100vh - 390px);
    max-height: calc(100vh - 390px);
    overflow-y: auto;
  }
}
