/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.PropertiesSideBar__container {
  width: 350px; }

.PropertiesSideBar__add-button-list {
  border: 0; }

.PageProperties__container .slds-section {
  margin-top: 16px; }

.PropertiesSideBar__container .Section__title--error {
  display: none; }

.PropertiesSideBar__container .PageProperties__container {
  border: 1px solid #dddbda;
  padding: 10px;
  border-radius: 4px;
  min-height: calc(100vh - 100px);
  position: relative; }

.Button.FieldProperties__remove-btn {
  margin-top: auto;
  width: max-content;
  margin-left: calc(50% - 80px); }

.sticky .PropertiesSideBar__container .PageProperties__container {
  overflow-y: auto;
  max-height: calc(100vh - 80px); }
