/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

body {
  margin: 0;
  padding: 0;
  font-family: "Salesforce Sans", "Arial", "Helvetica", "sans-serif";
  background-color: #fff; }

.layoutEditorContainer body {
  background-color: #fff; }

.Button {
  max-width: 300px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

#portal-root {
  position: relative;
  z-index: 5000; }

.Section__addon .Button--type-link, .PortalComponent .Button--type-link, .FieldProperties__lookup-container .Button--type-link, .PropertiesSideBar__container .Button--type-link, .ReusableHeader__mapping .Button--type-link, .ReusableHeader__form .Button--type-link {
  border: 0; }

.Section__addon .Button--type-link, .PortalComponent .Button--type-link, .FieldProperties__lookup-container .Button--type-link, .PropertiesSideBar__container .Button--type-link, .ReusableHeader__mapping .Button--type-link, .ReusableHeader__form .Button--type-link,
.Section__addon .Button--type-link:hover,
.PortalComponent .Button--type-link:hover,
.FieldProperties__lookup-container .Button--type-link:hover,
.PropertiesSideBar__container .Button--type-link:hover,
.ReusableHeader__mapping .Button--type-link:hover,
.ReusableHeader__form .Button--type-link:hover,
.Section__addon .Button--type-link:active,
.PortalComponent .Button--type-link:active,
.FieldProperties__lookup-container .Button--type-link:active,
.PropertiesSideBar__container .Button--type-link:active,
.ReusableHeader__mapping .Button--type-link:active,
.ReusableHeader__form .Button--type-link:active,
.Section__addon .Button--type-link:focus,
.PortalComponent .Button--type-link:focus,
.FieldProperties__lookup-container .Button--type-link:focus,
.PropertiesSideBar__container .Button--type-link:focus,
.ReusableHeader__mapping .Button--type-link:focus,
.ReusableHeader__form .Button--type-link:focus,
.Section__addon .Button--type-link:disabled,
