/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Picklist {
  outline: none; }
  .Picklist .Input {
    overflow: hidden;
    text-overflow: ellipsis; }
    .Picklist .Input[readonly]::placeholder, .Picklist .Input::placeholder {
      font-style: italic;
      color: #969492; }
  .Picklist .Menu {
    position: absolute;
    z-index: 20; }
  .Picklist .PillList {
    margin-top: unset;
    padding-top: unset;
    padding-right: unset; }

.slds-has-error .Input.slds-input:active,
.slds-has-error .Input.slds-input:focus {
  box-shadow: #c23934 0 0 0 1px inset, 0 0 3px #1589ee; }

/* stylelint-disable max-nesting-depth, selector-max-compound-selectors */
.slds-has-error .Picklist .Search .InputWrapper .Input {
  box-shadow: none;
  border-color: #dddbda; }

.slds-has-error .Picklist .Search .InputWrapper .Input.slds-input:focus,
.slds-has-error .Picklist .Search .InputWrapper .Input.slds-input:active {
  box-shadow: none; }

/* stylelint-enable */
.slds-has-error .Input.slds-input.slds-combobox__input {
  border-color: #c23934; }

.slds-has-error .Picklist__container .Pill__label {
  font-size: 0.875rem;
  color: #080707;
  font-weight: normal; }
