/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Badge {
  margin: 0 3px;
  display: inline-flex;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  min-width: 89px;
  max-width: 180px;
  text-align: center;
  cursor: default;
  vertical-align: middle;
  align-items: center;
  /* custom styles specifically for DCx badges */ }
  .Badge .Badge__label {
    display: inline-block;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align: middle; }
    .Badge .Badge__label .slds-icon_container {
      vertical-align: top; }
  .Badge.Badge--allow-min-width {
    min-width: 50px; }
  .Badge.Badge--has-icon.Badge--allow-min-width {
    min-width: 68px; }
  .Badge.Badge--allow-min-width.Badge--is-removable {
    min-width: 70px; }
  .Badge.Badge--has-icon.Badge--is-removable {
    min-width: 123px; }
  .Badge.Badge--has-icon.Badge--allow-min-width.Badge--is-removable {
    min-width: 94px; }
  .Badge.slds-badge_lightest {
    height: 25px;
    border: 1px solid #dddbda;
    background-color: white;
    font-size: 0.75rem;
    text-transform: none;
    letter-spacing: normal; }
    .Badge.slds-badge_lightest:hover {
      background-color: #e3e5ed; }
  .Badge .Badge__label--is-removable {
    width: calc(100% - 20px); }
  .Badge .Badge__label--icon-name-record {
    margin: 0 0 0 -7px;
    width: calc(100% + 7px); }
    .Badge .Badge__label--icon-name-record .Icon__container {
      vertical-align: baseline; }
    .Badge .Badge__label--icon-name-record .Badge__icon {
      margin-right: 0; }
  .Badge .Badge__close {
    float: right; }
    .Badge .Badge__close:focus, .Badge .Badge__close:active {
      outline: 0;
      box-shadow: none; }
    .Badge .Badge__close .Icon {
      position: absolute;
      top: 1px;
      right: -2px; }

.BadgeList {
  margin-top: 6px;
  display: inline-block;
  position: relative;
  vertical-align: middle; }
  .BadgeList .Badge {
    display: inline-block;
    position: relative;
    margin-left: 0;
    margin-right: 6px;
    margin-bottom: 4px; }

.BadgeList--stacked {
  margin: 0;
  vertical-align: middle;
  /* here to override an .slds-scope issue */ }
  .BadgeList--stacked .Badge__stack {
    margin: 0; }
  .BadgeList--stacked .Badge__stack + .Badge__stack {
    margin-left: 0; }
  .BadgeList--stacked .Badge__stack-1,
  .BadgeList--stacked .Badge__stack-2 {
    position: absolute;
    top: 0; }
  .BadgeList--stacked .Badge__stack-top {
    z-index: 9; }
  .BadgeList--stacked .Badge__stack-1 {
    left: 6px;
    z-index: 8; }
  .BadgeList--stacked .Badge__stack-2 {
    left: 12px;
    z-index: 7; }
  .BadgeList--stacked.BadgeList--stacked:hover .Badge {
    background-color: #e3e5ed; }
