/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Popover {
  display: inline-block; }
  .Popover::before {
    width: 1rem;
    height: 1rem;
    position: absolute;
    transform: rotate(45deg);
    content: '';
    background-color: inherit; }

.Popover__trigger {
  outline: none;
  display: inline-block; }

.Popover.Popover__content,
.slds-scope .Popover.Popover__content {
  position: absolute;
  top: 20px;
  z-index: 5000; }
  .Popover.Popover__content.slds-popover,
  .slds-scope .Popover.Popover__content.slds-popover {
    border: 1px solid #dddbda; }

.Popover-placement-bottom::before {
  left: 50%;
  top: -0.5rem;
  margin-left: -0.5rem;
  box-shadow: -1px -1px 0 0 rgba(0, 0, 0, 0.16); }

/* stylelint-disable selector-class-pattern  */
.Popover-placement-bottomLeft::before {
  left: 1.5rem;
  top: -0.5rem;
  margin-left: -0.5rem;
  box-shadow: -1px -1px 0 0 rgba(0, 0, 0, 0.16); }

.Popover-placement-bottomRight::before {
  left: auto;
  right: 1rem;
  top: -0.5rem;
  margin-left: -0.5rem;
  box-shadow: -1px -1px 0 0 rgba(0, 0, 0, 0.16); }

.Popover-placement-top::before {
  left: 50%;
  bottom: -0.5rem;
  margin-left: -0.5rem;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.16); }

.Popover-placement-topLeft::before {
  left: 1.5rem;
  bottom: -0.5rem;
  margin-left: -0.5rem;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.16); }

.Popover-placement-topRight::before {
  right: 1rem;
  bottom: -0.5rem;
  margin-left: -0.5rem;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.16); }

.Popover-placement-right::before {
  left: -0.5rem;
  top: 50%;
  margin-top: -0.5rem;
  box-shadow: -1px 1px 2px 0 rgba(0, 0, 0, 0.16); }

.Popover-placement-rightTop::before {
  left: -0.5rem;
  top: 1.5rem;
  margin-top: -0.5rem;
  box-shadow: -1px 1px 2px 0 rgba(0, 0, 0, 0.16); }

.Popover-placement-rightBottom::before {
  left: -0.5rem;
  bottom: 1rem;
  margin-top: -0.5rem;
  box-shadow: -1px 1px 2px 0 rgba(0, 0, 0, 0.16); }

.Popover-placement-left::before {
  right: -0.5rem;
  top: 50%;
  margin-top: -0.5rem;
  box-shadow: 1px -1px 2px 0 rgba(0, 0, 0, 0.16); }

.Popover-placement-leftTop::before {
  right: -0.5rem;
  top: 1.5rem;
  margin-top: -0.5rem;
  box-shadow: 1px -1px 2px 0 rgba(0, 0, 0, 0.16); }

.Popover-placement-leftBottom::before {
  right: -0.5rem;
  bottom: 1rem;
  margin-top: -0.5rem;
  box-shadow: 1px -1px 2px 0 rgba(0, 0, 0, 0.16); }

/* stylelint-enable */
