/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Dropdown {
  position: absolute !important;
  z-index: 7000;
  width: auto;
  background-color: #f3f2f2; }

.rc-trigger-popup {
  position: absolute;
  left: -9999px;
  top: -9999px;
  z-index: 1000; }

.Menu.slds-dropdown {
  position: static;
  left: initial;
  transform: none;
  float: none;
  outline: 0; }
