/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Toggle {
  display: inline-block;
  width: auto; }

/* stylelint-disable selector-class-pattern */
.slds-scope .slds-checkbox_toggle,
.slds-scope .slds-checkbox--toggle {
  width: auto; }

.slds-scope.slds-checkbox--toggle[type='checkbox']:checked + .slds-checkbox--faux_container .slds-checkbox--faux {
  border-color: #0070d2;
  background-color: #0070d2; }
  .slds-scope.slds-checkbox--toggle[type='checkbox']:checked + .slds-checkbox--faux_container .slds-checkbox--faux:hover, .slds-scope.slds-checkbox--toggle[type='checkbox']:checked + .slds-checkbox--faux_container .slds-checkbox--faux:focus {
    background-color: #005fb2; }

/* stylelint-enable */
