/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.PageLayout__field--container {
  outline: 0; }

.Draggable__field {
  padding-bottom: 20px; }

.Draggable__field--dragging .LayoutField__box {
  border: 2px dashed #dddbda; }

.PageLayout__field .LayoutField__text {
  height: 30px;
  white-space: normal; }

.PageLayout__field:hover .LayoutField__box,
.PageLayout__field:focus .LayoutField__box {
  border-color: #0070d2;
  outline: 0; }

.LayoutField.PageLayout__field.LayoutField--highlighted {
  outline: 0; }

.LayoutField.PageLayout__field.LayoutField--highlighted .LayoutField__box {
  border: 1px solid #0070d2;
  height: 32px; }

.LayoutField.PageLayout__field.PageLayout__field--error .LayoutField__box {
  border: 1px solid #c23934; }

.Placeholder__field .LayoutField__box {
  border: 1px dashed #dddbda; }
