.Pill {
  flex: 1 0;

  .Pill__label {
    flex: 1 0;
    margin-left: 0.5em;
    cursor: default;
    max-width: 100px;
    min-width: 50px;
  }

  &.Pill--truncated {
    .Pill__label {
      max-width: 100px;
    }
  }

  .Pill__icon {
    flex: 0 0 20px;
    margin-right: 0;
  }

  .Pill__remove {
    flex: 0 0 20px;
    width: inherit;
    height: inherit;
    margin-right: 0.25rem;
  }

  .Pill__remove-icon {
    margin: 0;
  }
}

.PillList {
  margin-top: 6px;
  padding: 0.25rem;
  display: inline-block;
  position: relative;
  vertical-align: middle;

  .Pill {
    position: relative;
    margin-left: 0;
    margin-right: 0.25rem;

    .Pill__label {
      font-size: 0.875rem;
    }

    &.Pill--disabled {
      cursor: not-allowed;
      user-select: none;

      .Pill__label {
        cursor: not-allowed;
      }
    }
  }
}

.PillList.PillList--unordered {
  vertical-align: middle;
  padding: 0;
  height: auto;
}

.PillList.slds-size--block {
  display: inline-block;
  padding: 0.25rem;
}
