/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.LayoutField {
  display: flex;
  align-items: center; }
  .LayoutField .LayoutField__box {
    width: 100%;
    display: flex;
    align-items: center;
    background-color: white;
    border: 1px solid #dddbda;
    border-radius: 0.25rem;
    padding: 0 0.5rem;
    line-height: 1.875rem; }
  .LayoutField .LayoutField__close {
    display: none; }
  .LayoutField.LayoutField--is-removable:hover .LayoutField__box {
    width: calc(100% - 30px);
    background-color: #f3f2f2; }
  .LayoutField.LayoutField--is-removable:hover .LayoutField__close {
    display: block;
    cursor: pointer;
    margin-left: 10px; }
  .LayoutField.LayoutField--highlighted {
    outline: auto #1589ee; }
  .LayoutField .LayoutField__text {
    flex: 1 0;
    padding-left: 5px; }
  .LayoutField.LayoutField__empty {
    border: 2px #dddbda dashed;
    height: 1.875rem; }
