/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.ReusableHeader__form {
  padding: 0 24px 0 5px; }
  .ReusableHeader__form .Button--type-link {
    border: 0;
    padding: 0;
    width: max-content; }
  .ReusableHeader__form .Button--type-link:hover {
    background: none; }

.ReusableHeader__dev-name--input::before,
.ReusableHeader__name--input label::before,
.ReusableHeader__object-selection label::before,
.ReusableHeader__target-object label::before {
  content: "* ";
  color: #c23934; }

.ReusableHeader__object-selection label + .FormElement.slds-form-element,
.ReusableHeader__target-object label + .FormElement.slds-form-element {
  padding-left: 0; }

.ReusableHeader__row {
  margin-bottom: 10px; }

.ReusableHeader__not-configured {
  text-align: right; }

.Label.ReusableHeader__not-configured {
  color: #d37300; }

.Grid.ReusableHeader__mapping-grid {
  display: block; }

.Grid__item .EntityPicker .FormElement.slds-form-element {
  padding-left: 0; }

.ReusableHeader__mapping-select {
  text-align: right; }

.Icon.SectionProperties__mapping-set--icon {
  fill: #0070d2;
  margin-right: 10px; }

.ReusableHeader__clipboard {
  margin-top: 26px;
  margin-right: 8px; }

.ReusableHeader__clipboard-btn {
  height: 30px;
  width: 30px;
  margin-top: 26px;
  border: 1px solid #dddbda; }

.ReusableHeader__name--input,
.ReusableHeader__dev-name--input {
  display: flex; }

.ReusableHeader__dev-name--input,
.ReusableHeader__description-input {
  margin-right: 20px; }

.Entity__title {
  display: flex;
  margin-right: 20px; }
  .Entity__title .ReusableHeader__title--toggle {
    margin-top: 28px; }

.ReusableHeader__tags-input {
  margin-right: 20px; }
  .ReusableHeader__tags-input .Typeahead__menu {
    width: 100%; }
