/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.LayoutEditor__section--related {
  width: 100%;
  overflow: hidden; }
  .LayoutEditor__section--related .Section.Section__bordered .Section__content {
    width: 100%;
    display: inline-grid;
    overflow: hidden;
    padding-bottom: 0; }
  .LayoutEditor__section--related .EmptyLayoutSectionMessage {
    padding-bottom: 12px; }
  .LayoutEditor__section--related .PageLayout__field {
    margin: 0;
    padding-right: 20px; }
  .LayoutEditor__section--related .Section.Section__bordered {
    margin-top: 0;
    margin-bottom: 0; }

.LayoutEditor__section--related.LayoutEditor__section--related-error {
  padding-top: 20px; }

.LayoutEditor__section--related-scroller {
  display: flex;
  min-width: 100%;
  overflow-x: auto;
  padding: 20px; }

.LayoutEditor__section--reorder .LayoutEditor__section--related-scroller {
  display: grid;
  row-gap: 20px;
  justify-content: center; }

.LayoutEditor__section--reorder .PageLayout__field {
  padding-right: 0; }

.LayoutEditor__section--reorder .LayoutField:hover {
  cursor: move; }

.LayoutEditor__section--reorder .LayoutField__empty {
  border-style: dashed;
  border-color: #dddbda;
  padding-right: 20px; }

.LayoutEditor__section--reorder .DraggableLayoutField__dragging {
  margin-right: 0; }

/* LayoutField__isRemovable is a component level css class */
/* stylelint-disable selector-class-pattern */
.DraggableLayoutField.DraggableLayoutField__dragging.LayoutField__isRemovable .LayoutField {
  padding: 0;
  cursor: move; }

.DraggableLayoutField.DraggableLayoutField__dragging.LayoutField__isRemovable .LayoutField:hover .LayoutField__close {
  display: none;
  cursor: move; }

/* stylelint-enable */
.DraggableLayoutField__dragging {
  margin-right: 20px; }

.DraggableLayoutField__empty {
  height: 35px;
  padding: 2px; }

.DraggableLayoutField__preview {
  cursor: move;
  display: none;
  opacity: 0.9; }

.DraggableLayoutField__preview.dragging {
  display: block;
  margin-top: -28px;
  cursor: move;
  opacity: 0.9; }

.DraggableLayoutField .LayoutField:hover {
  cursor: move; }

.RelatedSection__drop--target {
  width: 100%; }
  .RelatedSection__drop--target.dragging {
    opacity: 0.9;
    border: 2px #dddbda dashed; }
