/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Menu__list {
  max-height: 300px;
  overflow-y: auto; }

.Menu {
  z-index: 7000;
  display: none;
  min-height: 40px;
  padding: 0; }
  .Menu.Menu--always-open {
    z-index: auto; }
  .Menu.Menu--has-nubbin,
  .Menu.Menu--has-nubbin .Menu__list {
    border: 0; }
  .Menu .Menu__item {
    user-select: none; }
    .Menu .Menu__item .Icon.Icon--selected {
      fill: #1589ee; }
    .Menu .Menu__item .Menu__item-link {
      justify-content: normal; }
      .Menu .Menu__item .Menu__item-link.Menu__item--is-locked {
        cursor: not-allowed; }
      .Menu .Menu__item .Menu__item-link.Menu__item--is-readonly {
        color: #706e6b;
        cursor: not-allowed;
        /* stylelint-disable max-nesting-depth, selector-max-compound-selectors */
        /* stylelint-enable */ }
        .Menu .Menu__item .Menu__item-link.Menu__item--is-readonly .Icon.Icon--selected {
          fill: #514f4d; }
    .Menu .Menu__item .Menu__item--error-icon {
      display: inline-block;
      vertical-align: bottom;
      line-height: 1; }
    .Menu .Menu__item .slds-icon-red {
      fill: #d4504c;
      margin-left: 2px;
      margin-top: -10px; }
    .Menu .Menu__item.Menu__item--has-secondary .Menu__item-link {
      padding-top: 0.25rem;
      padding-bottom: 0.25rem; }
    .Menu .Menu__item.Menu__item--has-focus a {
      background-color: #f3f2f2;
      outline: 0;
      text-decoration: none; }
    .Menu .Menu__item .Icon {
      flex: none; }
    .Menu .Menu__item .MenuItem__content {
      flex: 0 1 auto; }
    .Menu .Menu__item .slds-text-color--weak {
      color: #b0adab;
      max-width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap; }
    .Menu .Menu__item .Menu__item--listbox-option,
    .Menu .Menu__item .Menu__item--listbox-option:hover {
      display: block; }
    .Menu .Menu__item .Menu__item--listbox-option[aria-selected='false'] {
      color: #3e3e3c; }
  .Menu.Menu--size-xx-small {
    width: 96px;
    max-width: 96px; }
  .Menu.Menu--size-x-small {
    width: 192px;
    max-width: 192px; }
  .Menu.Menu--size-small {
    width: 240px;
    max-width: 240px; }
  .Menu.Menu--size-medium {
    width: 320px;
    max-width: 320px; }
  .Menu.Menu--size-large {
    width: 400px;
    max-width: 400px; }
  .Menu.Menu--size-x-large {
    width: 640px;
    max-width: 640px; }
  .Menu.Menu--size-xx-large {
    width: 960px;
    max-width: 960px; }
  .Menu.Menu--size-block {
    width: 100%;
    max-width: 100%; }

.Menu__filter {
  margin: 0.25em 1em; }
  .Menu__filter .Search {
    width: 100%;
    min-width: 10rem; }

.Menu__footer,
.Menu__header {
  padding: 0.5rem 0.75rem; }
  .Menu__footer.Menu__item--is-clickable,
  .Menu__header.Menu__item--is-clickable {
    cursor: pointer; }
  .Menu__footer.Menu__item--is-clickable:hover,
  .Menu__header.Menu__item--is-clickable:hover {
    outline: 0;
    text-decoration: none;
    background-color: #f3f2f2; }

.Menu--is-visible {
  display: block; }
