/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.DuelingPicklist .DuelingPicklist__source-menu,
.DuelingPicklist .DuelingPicklist__target-menu,
.DuelingPicklist .DuelingPicklist__target-custom {
  min-height: 210px;
  max-height: 420px;
  box-shadow: none; }

.DuelingPicklist.DuelingPicklist--menu-block .DuelingPicklist__source-wrapper,
.DuelingPicklist.DuelingPicklist--menu-block .DuelingPicklist__target-wrapper {
  width: calc(50% - 44px); }

.DuelingPicklist .DuelingPicklist__target-custom.DataGrid {
  height: 15rem;
  border: 1px solid #dddbda;
  border-radius: 0.25rem;
  margin-top: 0.125rem;
  margin-bottom: 0.125rem; }

.DuelingPicklist .DuelingPicklist__control {
  flex: 0 0 38px;
  padding: 10px; }
  .DuelingPicklist .DuelingPicklist__control.DuelingPicklist__control--has-label {
    margin-top: 20px; }
  .DuelingPicklist .DuelingPicklist__control .Button {
    display: block;
    margin: 10px 0; }
    .DuelingPicklist .DuelingPicklist__control .Button.left {
      margin-left: 0; }

.DuelingPicklist .Menu__list {
  overflow-y: unset; }

.slds-has-error .DuelingPicklist__source-menu,
.slds-has-error .DuelingPicklist__target-menu,
.slds-has-error .DuelingPicklist__target-custom,
.slds-has-error .OrderingPicklist__list {
  border: 2px solid #c23934; }

.slds-has-error .DuelingPicklist__source-menu .Input.slds-input {
  border-color: #dddbda;
  box-shadow: none; }

.slds-has-error .DuelingPicklist__source-menu .Input.slds-input:active,
.slds-has-error .DuelingPicklist__source-menu .Input.slds-input:focus {
  outline: 0;
  border-color: #1589ee;
  background-color: white;
  box-shadow: 0 0 3px #0070d2; }
