.DateTimeInput {
  display: flex;

  .DateTimeInput__date {
    padding-right: 0.5rem;

    &.DateTimeInput--default {
      width: 100%;

      &.Datepicker {
        width: 333px;
      }
    }
  }

  &.DateTimeInput--responsive {
    .slds-size--x-small {
      flex: 1 0;
      width: 100%;
    }
  }
}
