/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.DataGridWithSelection .rt-thead .DataGridWithSelection__select-all .rt-resizable-header-content {
  /* stylelint-disable-line selector-max-compound-selectors */
  text-overflow: initial; }

.DataGridWithSelection--size-small {
  width: 240px;
  max-width: 240px; }

.DataGridWithSelection--size-medium {
  width: 320px;
  max-width: 320px; }

.DataGridWithSelection--size-large {
  width: 400px;
  max-width: 400px; }

.DataGridWithSelection--size-x-large {
  width: 640px;
  max-width: 640px; }

.DataGridWithSelection--size-xx-large {
  width: 960px;
  max-width: 960px; }

.DataGridWithSelection--size-block {
  width: 100%;
  max-width: 100%; }
