/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.ColorPicker__overlay {
  display: inline-block;
  background: white;
  border: 0 solid rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.25);
  width: 20rem;
  min-height: 9rem;
  z-index: 9002;
  /* stylelint-disable selector-class-pattern  */
  /* stylelint-enable */ }
  .ColorPicker__overlay::before {
    width: 1rem;
    height: 1rem;
    position: absolute;
    transform: rotate(45deg);
    content: '';
    background-color: inherit; }
  .ColorPicker__overlay .ColorPicker__popover .ColorPicker__container {
    padding: 15px 9px 9px 15px; }
  .ColorPicker__overlay .ColorPicker__popover .ColorPicker__swatch {
    display: inline-block; }
  .ColorPicker__overlay.ColorPicker__overlay-placement-bottomLeft::before {
    left: 1.5rem;
    top: -0.5rem;
    margin-left: -0.5rem;
    box-shadow: -1px -1px 0 0 rgba(0, 0, 0, 0.07); }
  .ColorPicker__overlay.ColorPicker__overlay-placement-bottomRight::before {
    left: auto;
    right: 1rem;
    top: -0.5rem;
    margin-left: -0.5rem;
    box-shadow: -1px -1px 0 0 rgba(0, 0, 0, 0.07); }
  .ColorPicker__overlay.ColorPicker__overlay-placement-top::before {
    left: 50%;
    bottom: -0.5rem;
    margin-left: -0.5rem;
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.07); }
  .ColorPicker__overlay.ColorPicker__overlay-placement-topLeft::before {
    left: 1.5rem;
    bottom: -0.5rem;
    margin-left: -0.5rem;
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.07); }
  .ColorPicker__overlay.ColorPicker__overlay-placement-topRight::before {
    right: 1rem;
    bottom: -0.5rem;
    margin-left: -0.5rem;
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.07); }
  .ColorPicker__overlay.ColorPicker__overlay-placement-right::before {
    left: -0.5rem;
    top: 50%;
    margin-top: -0.5rem;
    box-shadow: -1px 1px 2px 0 rgba(0, 0, 0, 0.07); }
  .ColorPicker__overlay.ColorPicker__overlay-placement-rightTop::before {
    left: -0.5rem;
    top: 1.5rem;
    margin-top: -0.5rem;
    box-shadow: -1px 1px 2px 0 rgba(0, 0, 0, 0.07); }
  .ColorPicker__overlay.ColorPicker__overlay-placement-rightBottom::before {
    left: -0.5rem;
    bottom: 1rem;
    margin-top: -0.5rem;
    box-shadow: -1px 1px 2px 0 rgba(0, 0, 0, 0.07); }
  .ColorPicker__overlay.ColorPicker__overlay-placement-left::before {
    right: -0.5rem;
    top: 50%;
    margin-top: -0.5rem;
    box-shadow: 1px -1px 2px 0 rgba(0, 0, 0, 0.07); }
  .ColorPicker__overlay.ColorPicker__overlay-placement-leftTop::before {
    right: -0.5rem;
    top: 1.5rem;
    margin-top: -0.5rem;
    box-shadow: 1px -1px 2px 0 rgba(0, 0, 0, 0.07); }
  .ColorPicker__overlay.ColorPicker__overlay-placement-leftBottom::before {
    right: -0.5rem;
    bottom: 1rem;
    margin-top: -0.5rem;
    box-shadow: 1px -1px 2px 0 rgba(0, 0, 0, 0.07); }

.ColorPicker__trigger {
  position: relative;
  width: 30px;
  height: 30px; }
  .ColorPicker__trigger.ColorPicker__trigger--disabled {
    cursor: not-allowed; }
