/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.CellEditPopover {
  width: auto; }

.CellEditPopover-content {
  padding: 3px; }
  .CellEditPopover-content .FormElement {
    display: flex;
    flex-wrap: nowrap;
    align-items: flex-start; }
    .CellEditPopover-content .FormElement.slds-has-error .FormElement__help {
      width: 15rem;
      white-space: normal;
      padding-left: 0; }
  .CellEditPopover-content .CellEditPopover__extended-edit-trigger {
    margin-left: 3px; }

.slds-scope .CellEditPopover.slds-popover {
  width: auto; }
