/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Section .Section__title {
  position: relative; }
  .Section .Section__title .slds-button:focus {
    box-shadow: none; }

.Section .Section__title--error {
  position: absolute;
  top: -20px;
  right: 0;
  z-index: 6; }
  .Section .Section__title--error .Icon {
    color: #c23934; }

.Section.slds-is-open .Section__content {
  overflow: visible; }

.Section.Section__bordered {
  background-color: #f3f2f2;
  border: 1px #ecebea solid; }
  .Section.Section__bordered .Section__title {
    border-radius: 0;
    border-bottom: 1px #ecebea solid; }
    .Section.Section__bordered .Section__title .slds-button.slds-section__title-action {
      background-color: inherit;
      cursor: default; }
  .Section.Section__bordered .Section__content {
    padding-bottom: 12px; }

.Section.Section--highlighted {
  border: 2px solid #1589ee; }

.Section.Section--error {
  border: 2px solid #c23934; }

.Section__title--action {
  display: flex;
  flex: 1 0;
  justify-content: space-between;
  overflow: hidden;
  outline: 0; }
  .Section__title--action > span {
    display: flex;
    align-items: center; }
    .Section__title--action > span .slds-section__title-action-icon {
      flex: 0 0 14px; }
    .Section__title--action > span .Section__title--text {
      white-space: nowrap; }
    .Section__title--action > span .Section__subtitle {
      font-size: 14px;
      font-weight: 200;
      color: #969492; }
