/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

/* stylelint-disable-next-line */
.DataGrid .rt-thead .rt-th.DataTableCell__header--align-center > div:first-child {
  padding-left: 15px; }

.DataTableCell--no-overflow {
  text-overflow: none; }

.DataTableCell--overflow .ObjectRelation,
.DataTableCell--overflow .Popover,
.DataTableCell--overflow .Popover__trigger {
  display: inline; }

.DataTableCell--overflow .Popover .Text {
  color: #0070d2; }

.ActionMenu__column {
  display: flex;
  align-items: flex-end;
  text-align: right;
  justify-content: flex-end; }

.DataGrid .rt-tbody {
  position: initial; }

.DataGrid__cell .NameCell__invalid-object {
  color: #c23934; }

.DataGrid__cell .NameCell__title-color {
  color: #006dcc; }

.DataGrid__cell .Button--type-link.NameCell {
  line-height: 1.5;
  display: inline-block; }

.NameCell__wrapper {
  overflow: hidden;
  text-overflow: ellipsis;
  display: flex;
  align-items: center;
  justify-content: center; }

.NameCell {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.DataGrid.DataGrid__list-view .Icon.NameCell__error-icon {
  fill: #c23934; }
