/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Tree {
  display: flex;
  height: 100%;
  flex-direction: column; }
  .Tree .Tree__branch {
    outline: 0; }
    .Tree .Tree__branch .Tree__branch-toggle-icon,
    .Tree .Tree__branch .Tree__branch-name {
      outline: 0; }
    .Tree .Tree__branch .Tree__branch-toggle-icon {
      margin-top: 0; }
  .Tree .Tree__data--is-scrollable {
    overflow: auto; }
  .Tree em {
    font-style: normal;
    background-color: #fff03f; }

.Tree__leaf .item-icon {
  margin-left: 8px; }

.Tree__leaf .slds-text-color--weak {
  color: #706e6b; }

.Tree__leaf .leaf-data {
  display: flex;
  width: 100%;
  min-width: 0; }

.Tree__leaf .leaf-action {
  display: flex;
  align-items: center;
  margin: 0 10px; }
  .Tree__leaf .leaf-action .Button {
    align-self: auto;
    margin-top: initial;
    border: 0; }
    .Tree__leaf .leaf-action .Button:focus {
      box-shadow: none; }
