/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.Typeahead .Typeahead__input {
  flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
  -webkit-flex-wrap: nowrap; }
  .Typeahead .Typeahead__input .slds-input::placeholder {
    color: #b0adab;
    font-style: italic; }

.Typeahead .Typeahead__menu {
  min-width: unset;
  max-width: unset;
  position: absolute;
  top: 31px; }
  .Typeahead .Typeahead__menu.v-align {
    bottom: 100%; }
  .Typeahead .Typeahead__menu.h-align {
    left: auto;
    right: 0; }
  .Typeahead .Typeahead__menu .Menu__list {
    max-height: 340px; }
  .Typeahead .Typeahead__menu .Menu__footer {
    background-color: #f3f2f2;
    text-align: center;
    line-height: 1.25;
    /* stylelint-disable */
    /* stylelint-enable */ }
    .Typeahead .Typeahead__menu .Menu__footer .slds-text-heading--label {
      text-transform: none;
      font-weight: bold; }

.Typeahead .TagTypeahead__list {
  margin-top: 5px; }
