/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src: url("/assets/fonts/webfonts/SalesforceSans-Italic.woff2") format("woff2"), url("/assets/fonts/webfonts/SalesforceSans-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap; }

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/
html {
  font-family: "Salesforce Sans", Arial, Helvetica, sans-serif;
  font-size: 16px; }

.GeneralSection__property--level1 {
  padding-left: 15px;
  padding-bottom: 15px; }
  .GeneralSection__property--level1 .SectionProperties__default--label {
    width: 170px; }
  .GeneralSection__property--level1 .Button--type-link {
    line-height: 1.5; }
  .GeneralSection__property--level1 .Icon.SectionProperties__mapping-set--icon {
    fill: #0070d2;
    margin-right: 10px; }

.SectionTitle {
  display: flex; }

.SectionTitle__toggle {
  padding-top: 28px; }
  .SectionTitle__toggle .FormElement.slds-form-element {
    margin: 0; }

.SectionProperties__default-values-text {
  padding-left: 0; }

.SectionProperties__columns {
  padding-bottom: 8px;
  padding-top: 8px; }

.SectionProperties__columns--options .slds-form-element__control {
  padding-top: 10px;
  display: flex; }

.SectionProperties__columns--option {
  padding-left: 15px; }

.SectionProperties__columns-objectlabel {
  font-size: 15px;
  margin-top: 5px; }

.SectionProperties__columns-objectnamevalue {
  font-size: 19px; }

.SectionProperties__related {
  padding-top: 12px; }
  .SectionProperties__related .FormElement.slds-form-element {
    padding-left: 0; }

.Section.Section--collapsed.slds-section .slds-section__content {
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  height: 0; }

.SectionProperties__radiogroup .slds-form-element__control {
  display: flex;
  flex-flow: row;
  justify-content: space-evenly; }

.FieldProperties__lookup-container .FormElement.slds-form-element {
  padding-bottom: 0; }

.Label.SectionProperties__notset--label {
  color: #d37300; }

.SectionProperties__sorting-container {
  padding-bottom: 20px; }

.SectionProperties__container {
  min-height: calc(100vh - 80px);
  display: flex;
  flex-direction: column; }

.SectionProperties__data-source {
  padding-bottom: 10px; }

.SectionProperties__extended-edit .FormElement.slds-form-element {
  padding-left: 0; }

.LookupSection--newmapping--button span {
  color: #3e3e3c;
  text-transform: uppercase;
  vertical-align: middle; }

.SectionProperties__related .EntityPicker .InputWrapper,
.SectionProperties__related .EntityPicker .Lookup__menu {
  width: 100%; }

.SectionProperties__display-rule-message {
  color: #c23934;
  padding-top: 0;
  margin-left: 5px; }

.SectionProperties__display-rule-title-with-tooltip {
  display: flex; }

.SectionProperties__display-rule-title {
  margin-right: 5px; }

.SectionProperties__display-rule-expr {
  padding-left: 20px; }

.add__mapping-section {
  float: left;
  margin-top: 10px;
  display: flex; }
  .add__mapping-section div {
    align-self: center;
    margin-left: 10px; }

.add__mapping-btn {
  color: #006dcc; }

.Additional__configurations {
  text-align: left;
  width: 95%; }
